nav {
    height: 70px;
    background-color: aqua;
}

.navbar {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-brand {
    font-size: 1.5rem;
    font-weight: 900;
    color: #353434;
}

.navbar-icon {
    margin-left: 15px;
}

.navbar-btn {
    width: 15%;
    display: flex;
    justify-content: space-between;
}

.navbar-signin {
    font-family: inherit;
    font-weight: 900;
    height: 30px;
    width: 80px;
    border: solid 1px #000;
    border-radius: 5px;
    background: linear-gradient(#eaece9, #c1bebe);
    cursor: pointer;
}

.navbar-signin:hover {
    background: #fff;
}

.navbar-signup {
    font-family: inherit;
    font-weight: 900;
    border: none;
    background: none;
    cursor: pointer;
}

.navbar-signup:hover {
    color: #fff;
    text-decoration: underline;
}

.main {
    width: 40%;
    margin: 0 auto;
    padding-top: 50px;
}

.title {
    display: flex;
    justify-content: center;
}

.title-input {
    background: transparent;
    font-family: inherit;
    font-size: 3rem;
    font-weight: 900;
}

.disabled {
    border: none;
}

.title-icon {
    font-size: 3rem;
    margin-left: 20px;
    margin-top: 10px;
    cursor: pointer;
}

#hide {
    font-size: 3rem;
    visibility: hidden;
    position: fixed;
    left: 0;
    padding: 10px;
}

.create-area {
    margin-top: 40px;
    margin-bottom: 5px;
    text-align: center;
}

.create-form, .search-form {
    position: relative;
}

.create-input, .search-input {
    width: 100%;
    height: 50px;
    padding: 20px;
    border-radius: 25px;
    font-family: inherit;
    font-size: 1.5rem;
}

.create-btn, .delete-btn {
    position: absolute;
    top: 8px;
    right: 10px;
    border: none;
    background: transparent;
    cursor: pointer;
    font-size: 2rem;
}

.title-icon:hover, .create-btn:hover, .delete-btn:hover {
    color: red;
}

.search {
    margin-bottom: 40px;
}

.search-input {
    margin-bottom: 10px;
}

.search-check {
    padding-left: 20px;
}

.search-btn {
    position: absolute;
    top: 8px;
    right: 10px;
    border: none;
    background: transparent;
    font-size: 2rem;
}

.item {
    position: relative;
    width: 100%;
    height: 50px;
    padding: 5px 20px;
    margin-bottom: 5px;
    border: solid 2px #000;
    background: #fff;
    font-family: inherit;
    font-size: 1.5rem;
}

.item-checkbox {
    transform: scale(2);
    margin-right: 20px;
}

.item-done {
    color: #353434;
    text-decoration: line-through;
}
